
.schools {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  overflow-x: hidden;
  background: #030f30;
  text-align: center;
}
.schools .content-wrapper {
  padding-top: 1.2rem;
}
.schools .content-wrapper h1 {
  width: 2.79rem;
  height: 0.72rem;
  margin-left: -0.3rem;
  background-image: url(common_title.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  margin-bottom: 0.8rem;
  color: #00f7ff;
  font-style: italic;
}
.schools .content-wrapper h1 span {
  height: 0.68rem;
  width: 2rem;
  right: 0;
  top: 0;
  text-align: center;
  font-size: 0.36rem;
  line-height: 0.68rem;
  position: absolute;
}
.schools .content-wrapper .logo {
  display: inline-block;
  width: 1.5rem;
  margin-top: 0.4rem;
}
.schools .content-wrapper .map {
  height: 3.55rem;
  width: 100%;
  position: relative;
  overflow: hidden;
  margin-bottom: 0.2rem;
}
.schools .content-wrapper .map .con {
  position: absolute;
  width: 8.8rem;
  height: 4.85rem;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(map.jpg);
  transform: translate(-1.2rem, -0.65rem);
}
.schools .content-wrapper .map .con .item {
  position: absolute;
  width: 3.11rem;
  height: 0.8rem;
  background-image: url(map_li.png);
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
}
.schools .content-wrapper .map .con .item a {
  color: #00f7ff;
}
.schools .content-wrapper .map .con .item span {
  display: block;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: left;
  font-size: 0.2rem;
  box-sizing: border-box;
  padding: 0 0.2rem;
  overflow: hidden;
}
.schools .content-wrapper .remark {
  padding: 0.4rem 0.8rem;
}
.schools .content-wrapper .remark p {
  font-size: 0.24rem;
  line-height: 1.5em;
  margin-bottom: 0.2rem;
}



