
.menu {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  transition: all 0s linear;
}
.menu.page-enter-active,
.menu.page-leave-active {
  /*transform: translateX(0);*/
  opacity: 1;
}
.menu.page-enter,
.menu.page-leave-active {
  /*transform: translateX(0);*/
  opacity: 0;
}
.menu .wrapper {
  transition: opacity 0.4s ease;
}
.menu .wrapper ul {
  position: absolute;
  width: 100%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.menu .wrapper ul li {
  width: 4.22rem;
  height: 0.91rem;
  background-image: url(menu_li.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 0.4rem;
  position: relative;
  transform: translateX(-100%);
  opacity: 0;
}
.menu .wrapper ul li:nth-child(1) {
  animation: menuLi 1s ease forwards;
}
.menu .wrapper ul li:nth-child(2) {
  animation: menuLi 1s 0.2s ease forwards;
}
.menu .wrapper ul li:nth-child(3) {
  animation: menuLi 1s 0.4s ease forwards;
}
.menu .wrapper ul li:nth-child(4) {
  animation: menuLi 1s 0.6s ease forwards;
}
.menu .wrapper ul li:nth-child(5) {
  animation: menuLi 1s 0.8s ease forwards;
}
.menu .wrapper ul li:nth-child(6) {
  animation: menuLi 1s 1s ease forwards;
}
.menu .wrapper ul li a {
  position: absolute;
  width: 2rem;
  height: 0.6rem;
  top: 0.05rem;
  right: 0;
  text-align: center;
  line-height: 0.6rem;
  color: #00f7ff;
  font-weight: bold;
  font-style: italic;
  letter-spacing: 2px;
  font-size: 0.36rem;
}
.menu .wrapper ul li:last-child {
  margin-bottom: 0;
}
@keyframes menuLi {
0% {
    transform: translateX(-100%);
    opacity: 0;
}
100% {
    transform: translateX(0);
    opacity: 1;
}
}



