
.school {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  overflow-x: hidden;
  overflow-y: auto;
  padding-top: 0.6rem;
  box-sizing: border-box;
}
.school .content-wrapper {
  min-height: 100%;
}
.school .content-wrapper .content-main {
  padding-bottom: 0.6rem;
}
.school .content-wrapper .content-main .title {
  width: 4.9rem;
  height: 1rem;
  background-image: url(school_name.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  margin-left: 0.4rem;
  box-sizing: border-box;
  padding: 0 0.1rem 0 0.25rem;
  line-height: 0.64rem;
  font-size: 0.24rem;
  font-weight: bold;
  overflow: hidden;
  margin-bottom: -0.1rem;
  color: #00f7ff;
}
.school .content-wrapper .content-main .detailCon {
  width: 5.98rem;
  height: 7.76rem;
  margin: 0 auto;
  background-image: url(school_bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  box-sizing: border-box;
  padding: 0.6rem 0.7rem 0.6rem 0.6rem;
}
.school .content-wrapper .content-main .detailCon ul {
  display: block;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.school .content-wrapper .content-main .detailCon ul li {
  height: 1.1rem;
  overflow: hidden;
  max-height: 1.1rem;
  transition: all 0.4s ease;
}
.school .content-wrapper .content-main .detailCon ul li.selected .base {
  color: #6f6f6f;
}
.school .content-wrapper .content-main .detailCon ul li.selected .base .select {
  border: 1px solid #23dee4;
}
.school .content-wrapper .content-main .detailCon ul li.selected .base .select:after {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  content: " ";
  background-image: url(selected.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 0.49rem auto;
}
.school .content-wrapper .content-main .detailCon ul li.notAble {
  color: #6f6f6f;
}
.school .content-wrapper .content-main .detailCon ul li.notAble .base .con h1 {
  color: #6f6f6f;
}
.school .content-wrapper .content-main .detailCon ul li.notAble .base .con p {
  color: #6f6f6f;
}
.school .content-wrapper .content-main .detailCon ul li.notAble .base .select {
  border: 1px solid #6f6f6f;
}
.school .content-wrapper .content-main .detailCon ul li .base {
  height: 1.1rem;
  box-sizing: border-box;
  padding: 0.2rem 0;
  position: relative;
  border-bottom: 1px dotted rgba(0, 247, 255, 0.5);
}
.school .content-wrapper .content-main .detailCon ul li .base .con {
  margin-right: 0.65rem;
}
.school .content-wrapper .content-main .detailCon ul li .base .con h1 {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.28rem;
  line-height: 1em;
  margin-bottom: 0.2rem;
  color: #00f7ff;
}
.school .content-wrapper .content-main .detailCon ul li .base .con p {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.22rem;
  line-height: 1em;
  color: #20c3a9;
}
.school .content-wrapper .content-main .detailCon ul li .base .select {
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  border: 1px solid #23dee4;
  right: 0;
  top: 0.25rem;
}
.school .content-wrapper .content-main .detailCon ul li .detail {
  /*border-bottom: 1px dotted #fff;*/
  /*padding-bottom: 0.5rem;*/
}
.school .content-wrapper .content-main .detailCon ul li .detail p {
  font-size: 0.22rem;
  line-height: 1.5em;
  text-indent: 2em;
  color: #279396;
}
.school .content-wrapper .content-main .detailCon ul li .detail img {
  max-width: 100%;
  margin: 0.2rem 0;
  border: 1px solid #00f7ff;
  box-sizing: border-box;
  border-radius: 8px;
}
.school .content-wrapper .btnGroup {
  width: 100%;
  left: 0;
  text-align: center;
  margin-top: 0.2rem;
}
.school .content-wrapper .btnGroup .button {
  width: 1.6rem;
  height: 0.6rem;
  text-align: center;
  line-height: 0.6rem;
  margin: 0 0.5rem;
  background-color: #030f33;
  display: inline-block;
  font-size: 0.36rem;
  box-shadow: 0 0 10px #287be6;
  color: #23dee4;
}
.school .content-wrapper .btnGroup .button a {
  color: #23dee4;
}



