
.alert {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.5s ease;
}
.alert.fade-enter-active,
.alert.fade-leave-active {
  opacity: 1;
}
.alert.fade-enter,
.alert.fade-leave-active {
  opacity: 0;
}
.alert .content-wrapper {
  width: 4.4rem;
  position: absolute;
  padding-top: 0.3rem;
  background-color: #fff;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 5px;
  overflow: hidden;
}
.alert .content-wrapper .content-main h1 {
  color: #a00;
  font-size: 0.36rem;
  text-align: center;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 0.4rem;
}
.alert .content-wrapper .content-main p {
  font-size: 0.28rem;
  color: #000;
  text-align: center;
  padding: 0 0.2rem;
  line-height: 1.5em;
}
.alert .content-wrapper .content-main .btnGroup {
  height: 0.6rem;
  line-height: 0.6rem;
  margin-top: 0.3rem;
  background-color: #051254;
  display: -ms-flexbox;
  display: flex;
  color: #fff;
}
.alert .content-wrapper .content-main .btnGroup .button {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  border-right: 1px solid #fff;
  font-size: 0.3rem;
  text-shadow: 0 0 5px #000;
}
.alert .content-wrapper .content-main .btnGroup .button:last-child {
  border-right: none;
}



