
.profile {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding-top: 0.8rem;
  padding-bottom: 0;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
}
.profile h1 {
  width: 2.8rem;
  height: 0.72rem;
  margin-left: -0.3rem;
  margin-bottom: 0.2rem;
  background-image: url(profile_title.png);
  background-size: 100% auto;
  background-repeat: no-repeat;
  position: relative;
  color: #00f7ff;
  font-weight: bold;
  font-style: italic;
}
.profile h1 span {
  height: 0.68rem;
  width: 2rem;
  position: absolute;
  right: 0;
  top: 0;
  line-height: 0.68rem;
  text-align: center;
  font-size: 0.36rem;
}
.profile .content-wrapper {
  width: 6rem;
  margin: 0 auto;
  background-image: url(profile_bg.png);
  background-size: 100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  height: 8.54rem;
  box-sizing: border-box;
  padding: 0.6rem 0.6rem 0.6rem 0.7rem;
}
.profile .content-wrapper .content-main {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}
.profile .content-wrapper .content-main li {
  height: 0.76rem;
  max-height: 0.76rem;
  margin-bottom: 0.4rem;
  transition: all 0.5s ease;
  overflow: hidden;
}
.profile .content-wrapper .content-main li .head {
  height: 3.24rem;
  background-image: url(head_bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}
.profile .content-wrapper .content-main li .head i {
  position: absolute;
  width: 1.55rem;
  padding-top: 1.55rem;
  left: 50%;
  top: 0.6rem;
  border-radius: 3px;
  transform: translateX(-46%);
  overflow: hidden;
  border: 1px solid #00f7ff;
}
.profile .content-wrapper .content-main li .head i img {
  position: absolute;
  width: 1.55rem;
  left: 0;
  top: 50%;
  border: none;
  transform: translateY(-50%);
  border-radius: 3px;
  margin: 0;
}
.profile .content-wrapper .content-main li .title {
  height: 0.76rem;
  line-height: 0.76rem;
  background-image: url(profile_name.png);
  background-size: 100% 100%;
  box-sizing: border-box;
  padding: 0 0.2rem;
  font-size: 0.3rem;
  margin-bottom: 0.2rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #00f7ff;
  position: relative;
}
.profile .content-wrapper .content-main li p {
  line-height: 1.5em;
  text-indent: 2em;
  padding: 0 0.05rem;
  color: #23dee4;
}
.profile .content-wrapper .content-main li img {
  margin: 0.2rem auto;
  display: block;
  max-width: 100%;
  border: 1px solid #00f7ff;
  box-sizing: border-box;
  border-radius: 8px;
}



