
#app {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 10;
}
#app .bg {
  background-image: url(../static/images/bg.jpg);
}
#app .pageBg {
  background-image: url(../static/images/pageBg.jpg);
  z-index: 20;
}
#app .pageBg.fade-enter-active,
#app .pageBg.fade-leave-active {
  transition: all 0.4s ease;
}
#app .pageBg.fade-enter,
#app .pageBg.fade-leave-active {
  opacity: 0;
}
#app .pageBg.fade-leave-active {
  z-index: 5;
}
#app.active {
  display: block;
}
#app.active .content ul li:nth-child(1) {
  transform: translateX(0);
}
#app.active .content ul li:nth-child(2) {
  transform: translateX(0);
}
#app.active .content .logo1 {
  transform: translateX(-50%) scale(1);
}
#app.active .content .logo2 {
  transform: translateX(-50%) translateY(0);
}
#app .content {
  position: relative;
  height: 100%;
  z-index: 10;
}
#app .content ul {
  width: 5rem;
  position: absolute;
  left: 0.8rem;
  bottom: 50%;
  margin-bottom: -0.2rem;
}
#app .content ul li {
  height: 1.55rem;
  margin-bottom: 0.5rem;
  background-size: 100% auto;
  background-position: center;
  background-repeat: no-repeat;
  transition: transform 1s ease;
}
#app .content ul li:after {
  content: '点击进入报名通道';
  position: absolute;
  width: 100%;
  text-align: center;
  font-size: 10px;
  top: 100%;
  color: #158084;
}
#app .content ul li a {
  display: block;
  height: 100%;
  text-indent: -9999rem;
}
#app .content ul li:nth-child(1) {
  background-image: url(../static/images/label2.png);
  transform: translateX(-100%);
}
#app .content ul li:nth-child(2) {
  background-image: url(../static/images/label1.png);
  transform: translateX(100%);
}
#app .content .logo1 {
  width: 1.96rem;
  left: 50%;
  transition: transform 1s ease;
  transform: translateX(-50%) scale(2);
  top: 50%;
}
#app .content .logo2 {
  width: 3.77rem;
  left: 50%;
  transition: transform 1s ease;
  transform: translateX(-50%) translateY(100%);
  bottom: 0.3rem;
}



